@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?3aooof');
  src:  url('fonts/icomoon.eot?3aooof#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?3aooof') format('truetype'),
    url('fonts/icomoon.woff?3aooof') format('woff'),
    url('fonts/icomoon.svg?3aooof#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

